@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
.userspacks {
  background: linear-gradient(#fdc46f, 50%, #fcfcfc);
}
.userpacksdeletetext {
  font-size: 18px;
  color: #fb6e6e;
  margin-top: 10px;
  margin-left: 8px;
  margin-bottom: 0;
}
.userpacksdelete {
  height: 38.5px;
  align-items: center;
  border: 1px solid #fb6e6e;
  cursor: pointer;
  border-radius: 5px;
}
.userpacksdelete:hover {
  background: linear-gradient(rgb(255 120 120), 45%, rgb(234 161 161));
}
.userpacksdelete:hover .userpacksdeletetext {
  color: #fff !important;
}
.userpackspreviewtext {
  font-size: 18px;
  color: #fdc46f;
  margin-top: 10px;
  margin-left: 8px;
  margin-bottom: 0;
}
#userpackspreview {
  height: 38.5px;
  align-items: center;
  border: 1px solid #fdc46f;
  cursor: pointer;
  border-radius: 5px;
}
#userpackspreview:hover {
  background: linear-gradient(#fdc46f, 45%, #f1ebac);
}
#userpackspreview:hover .userpackspreviewtext {
  color: #fff !important;
}
.userpackscardheader {
  justify-content: space-between;
  color: #fff;
  background: linear-gradient(rgb(255 120 120), 85%, rgb(234 161 161));
}
.userpackspkname {
  height: 38.5px;
  align-items: center;
  border: none;
}
.userpackspknametext {
  color: #fdc46f;
}
.userpackspendingimg {
  width: 23.5px;
  height: 23.5px;
  margin: 5px 11px 0 8px;
}
.userpacksitems {
  font-size: 18px;
  color: #fb6e6e;
  margin-left: 8px;
  margin-top: 10px;
  margin-bottom: 3px;
}
.userpacksdoneicon {
  fill: #fdc46f !important;
  margin: 5px 7px;
}
.userpacksproceed {
  margin-top: 25px !important;
  border: 2px solid#fdc46f !important;
  outline: none !important;
  cursor: pointer;
  color: #fdc46f !important;
  font-size: 18px !important;
  border-radius: 20px;
  display: inline-block;
  padding: 6px 18px !important;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  background: linear-gradient(90deg, #fdc46f 50%, #fff 0);
  background-size: 210% 100%;
  background-position: 100% 100%;
  transition: all 0.5s ease-out;
}
.userpacksproceed:hover {
  background-position: left bottom;
  color: #ffffff !important;
}

#mainNav {
  padding-top: 0.3rem !important;
  padding-bottom: 0.3rem !important;
}
.navbar-nav li a {
  color: black;
}
#navbarResponsive .navbar-nav li a {
  color: black !important;
}
#page-top
  .navbar
  .container
  .navbar-shrink
  .container
  .nav-bar
  #navbarResponsive
  .navbar-nav
  li
  a {
  color: white !important;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.column {
  flex-direction: column;
}
.nav-title {
  font-size: 10px !important;
  color: #7f7575 !important;
}
#mainNav .navbar-toggler {
  padding: 0.75rem;
  font-family: "Roboto", sans-serif;
  /* font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; */
  font-size: 0.75rem;
  color: #fff;
  text-transform: uppercase;
  background-color: #fdc673;
  border: 0;
}
#mainNav .navbar-brand {
  color: #fed136;
  font-family: "Roboto", sans-serif;
  /* font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; */
  font-weight: 700;
  letter-spacing: 0.0625em;
  text-transform: uppercase;
}
#mainNav .navbar-brand img {
  height: 1.5rem;
}
#mainNav .navbar-nav .nav-item .nav-link {
  font-family: "Roboto", sans-serif;
  /* font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; */
  font-size: 0.95rem;
  color: rgb(255, 255, 255);
  letter-spacing: 0.0625em;
}
#mainNav .navbar-nav .nav-item .nav-link.active,
#mainNav .navbar-nav .nav-item .nav-link:hover {
  color: rgb(66 66 66);
}

@media (min-width: 992px) {
  #mainNav {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    border: none;
    background-color: transparent;
    transition: padding-top 0.3s ease-in-out, padding-bottom 0.3s ease-in-out;
  }
  #mainNav .navbar-brand {
    font-size: 1.5em;
    transition: font-size 0.3s ease-in-out;
  }
  #mainNav .navbar-brand img {
    height: 2rem;
    transition: height 0.3s ease-in-out;
  }
  #mainNav.navbar-shrink {
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: linear-gradient(#fdc46f, #f9deb6);
  }
  #mainNav.navbar-shrink .navbar-brand {
    font-size: 1.25em;
  }
  #mainNav.navbar-shrink .navbar-brand svg,
  #mainNav.navbar-shrink .navbar-brand img {
    height: 1.5rem;
  }
  #mainNav .navbar-nav .nav-item {
    margin-right: 1rem;
  }
  #mainNav .navbar-nav .nav-item:last-child {
    margin-right: 0;
  }
}
.dropdown-menu,
.dropdown-menu-right,
.dropdown-secondary {
  background: #fb6e6e !important;

  border: 0px !important;
}

/*
  =========================================================================================
                                    Social Icons
  =========================================================================================
  */

.footer-logo img {
  height: 100px;
  object-fit: cover;
}
.footer-logo h1 {
  color: #fff;
  font-family: "Kaushan Script", cursive;
}
.round-btn {
  display: inline;
  height: 40px;
  width: 40px;
  background: #fff;
  border-radius: 50%;
  float: left;
  margin: 15px 8px;
  box-shadow: 2px 2px 5px 0px rgb(82, 0, 67);
  border: 1px solid; /*border: 1px solid #622657;*/
  cursor: pointer;
}
.round-btn a {
  display: block !important;
  padding: 7px 12px;
  font-size: 18px;
  border-radius: 50%;
  cursor: pointer;
}
.round-btn .icon {
  padding: 3px;
}
.round-btn .icon img {
  height: 24px;
  width: 32px;
  margin-top: 6px;
}
.btn-facebook a {
  color: #3b5998;
  padding: 8px 13px;
}
.btn-linkedin a {
  color: #007bb6;
}
.btn-twitter a {
  color: #1c9deb;
}
.btn-instagram a {
  color: #dd3f5c;
}
.btn-whatsapp a {
  color: #155e54;
}
.btn-envelop a {
  color: #d6403a;
  font-size: 15px;
  padding: 9px 12px;
}
.standard_header .standard_social_links {
  margin-left: 1rem;
}

